body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face { 
  font-family:  Komoda; 
  src: url(/D.-M.-Studio-animation/static/media/Komoda.142228c3.otf); 
} 

* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html{
  box-sizing: border-box;
  font-size: 62.5%; /* So that 1rem = 10px (10px/16px (browsers default) = 62.5% -> 1rem = 10px */
}

body{
  font-family: "sans-serif";
  font-weight: 300;
  color: var(--color-primary);
}

.App {
  width: 100%;
  /* Background Positioning */
  height: 100vh;
  background-image: url(/D.-M.-Studio-animation/static/media/DM-background.f6e15d2b.jpg);
  background-size: cover;
  background-position: top;
  position: relative;

  /* Grid */
  display: grid;
  grid-template-rows: [header-start] 3rem  [header-end full-start] calc(100vh - 3rem) [full-end]; 
  grid-template-columns: [sidebar-start] 3vw [sidebar-end full-start] 97vw [full-end];
}




/* Media Queries for large width */
@media only screen and (min-width: 75em) and (max-height: 740px){
  html{
      font-size: 50%;
  }
}
@media only screen and (min-width: 75em) and (max-height: 640px){
  html{
      font-size: 40%;
  }
}



/* Media Queries for medium width */
@media only screen and (max-width: 75em){
  html{
      font-size: 50%;
  }
}

@media only screen and (max-width: 75em) and (max-height: 650px){
  html{
      font-size: 40%;
  }
}
@media only screen and (max-width: 75em) and (max-height: 600px){
  html{
      font-size: 25%;
  }
}

/* Media Queries for small width */
@media only screen and (max-width: 768px){
  html{
      font-size: 25%;
  }
}



.navigation__checkbox{
    display: none;
}

.navigation__button{
    position: fixed;
    top: -2rem;
    right: 2rem;
    z-index: 1002;
    
    text-align: center;
    cursor: pointer;
}

.navigation__background{
    visibility: hidden;
    height: 2rem;
    width: 2rem;
    border-radius: 50%;
    position: fixed;
    top: 0.7rem;
    right: 2rem;
    background-image: radial-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8));
    z-index: 1;
    transition: all .8s cubic-bezier(0.86, 0, 0.07, 1);
}


.navigation__checkbox:checked ~ .navigation__background{
    visibility: visible;
    -webkit-transform: scale(200);
            transform: scale(200);
}

/* ICON */

.navigation__icon{
    position: relative;
    margin-top: 3.5rem;
}

.navigation__icon,
.navigation__icon::before,
.navigation__icon::after{
    height: .1rem;
    width: 1.5rem;
    background-color: black;
    display: inline-block;
}

.navigation__icon::before,
.navigation__icon::after{
    content: "";
    position: absolute;
    left: 0;
    transition: all .2s;
}

.navigation__icon::before{top: -0.5rem;}
.navigation__icon::after{top: 0.5rem;}


/* To make the navigation icon not shrink while font size is becoming smaller */
@media only screen and (max-height: 740px){
    .navigation__icon{
        height: .2rem;
    }
    .navigation__icon::before{top: -0.4rem;}
    .navigation__icon::after{top: 0.5rem;}
}


.navigation__checkbox:checked + .navigation__button .navigation__icon{
    background-color: transparent;
}

.navigation__checkbox:checked + .navigation__button .navigation__icon::before{
    background-color: white;
    top: 0;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}

.navigation__checkbox:checked + .navigation__button .navigation__icon::after{
    background-color: white;
    top: 0;
    -webkit-transform: rotate(-135deg);
            transform: rotate(-135deg);
}

/* Navigation Grid */

.navigation__fullscreen{
    margin-left: 3vw;
    margin-top: 34rem;
    width: 97vw;
    max-height: 100vh;
    position: absolute;
}

.navigation-grid{
    color: white;
    opacity: 1;

    font-family: Komoda;
    font-weight: 600;
    font-size: 10rem;
    letter-spacing: .5rem;
    display: grid;
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: repeat(17, 1fr);
}

/* Z-index is 2, so that it can stay on top when the Navigation is open */

/* GRID POSITIONING */
/* ROW 1 */
#ourServices-text{
    grid-column: 1 / 8;
    grid-row: 1;
    z-index: 2;
}
#navline1{
    grid-column: 8 / 17;
}

/* ROW 2 */
#navline2 {
    grid-column: 1 / 5;
}

#whoWeAre-text{
    grid-column: 5 / 12;
    grid-row: 2;
    z-index: 2;
}

#navline3 {
    z-index: 2;
    grid-column: 12 / 17;
}

/* ROW 3 */
#hotCases-text{
    z-index: 2;
    grid-column: 1 / 7;
}
#navline4{
    z-index: 2;
    grid-column: 7 / 9;
}
#letsTalk-text{
    z-index: 2;
    grid-column: 9 / 17;
}

/* LETTER POSITIONING */
.nav-ourServices,
.nav-whoWeAre,
.nav-line,
.nav-hotCases,
.nav-letsTalk{
    visibility: hidden;
    position: relative;
    display: flex;
    justify-content: space-around;
    padding: 1rem;/*
    margin-left: 4rem;
    margin-right: 4rem;*/
    transition: opacity 1s cubic-bezier(0,0,0,1);
}

.nav-line{
    display: block;
    height: 1px;
    border: 0;
    border-top: 0.2rem solid white;
    padding-bottom: 0;

    display: flex;
    align-self: center;
    z-index: 2;
}

.nav-line__text{
    color: white;
    font-family: "sans-serif";
    font-size: 1.7rem;
    font-weight: 600;
    letter-spacing: .2rem;
    -webkit-transform: translateY(-6rem);
            transform: translateY(-6rem);
}

/* ANIMATIONS */
.animation1-TextUp{
    -webkit-animation: moveInBottom .4s ease-out;
            animation: moveInBottom .4s ease-out;
    visibility: visible !important;
}

.animation2-TextUp{
    -webkit-animation: moveInBottom2 .4s ease-out;
            animation: moveInBottom2 .4s ease-out;
    visibility: visible !important;
}

.animation3-LineExpand{
    -webkit-animation: LineExpand .4s ease-out;
            animation: LineExpand .4s ease-out;
    visibility: visible !important;
}


/* MEDIA QUERIES */
@media only screen and (max-height: 600px){
    .navigation__fullscreen{
        margin-top: 25rem;
    }
}

/* ANIMATION KEYFRAMES */

/* For D.M. Studio text */
@-webkit-keyframes moveInBottom {
    0%{
        opacity: 0;
        -webkit-transform: translateY(-2rem);
                transform: translateY(-2rem);
    }
    100%{
        opacity: 1;
        -webkit-transform: translateY(-6rem);
                transform: translateY(-6rem);;
    }
}
@keyframes moveInBottom {
    0%{
        opacity: 0;
        -webkit-transform: translateY(-2rem);
                transform: translateY(-2rem);
    }
    100%{
        opacity: 1;
        -webkit-transform: translateY(-6rem);
                transform: translateY(-6rem);;
    }
}

/* For Navigation Menu Items*/
@-webkit-keyframes moveInBottom2 {
    0%{
        opacity: 0;
        -webkit-transform: translateY(3rem);
                transform: translateY(3rem);
    }
    100%{
        opacity: 1;
        -webkit-transform: translateY(0rem);
                transform: translateY(0rem);
    }
}
@keyframes moveInBottom2 {
    0%{
        opacity: 0;
        -webkit-transform: translateY(3rem);
                transform: translateY(3rem);
    }
    100%{
        opacity: 1;
        -webkit-transform: translateY(0rem);
                transform: translateY(0rem);
    }
}

/* For Navigation Line */
@-webkit-keyframes LineExpand {
    0%{
        width: 0;
    }
    100%{
        width: 100%;
    }
}
@keyframes LineExpand {
    0%{
        width: 0;
    }
    100%{
        width: 100%;
    }
}
.header{
    grid-column: 1 / -1;
    grid-row: header-start / header-end;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-text{
    font-size: 1rem;
    font-weight: 600;
    letter-spacing: .2rem;
    color: white;

    margin-left: 2rem;
}


.main{
    color: black;
    opacity: 1;
    grid-column: full-start / full-end;
    grid-row: full-start / full-end;

    font-family: Komoda;
    font-weight: 600;
    font-size: 17rem;

    display: grid;
    grid-template-rows: repeat(3, 1fr);
    grid-template-columns: repeat(3, 1fr);
}

/* Z-index is 2, so that it can stay on top when the Navigation is open */
#we-text{
    z-index: 2;
}
#are-text{
    z-index: 2;
}

/* Positioning of the two other texts in the Grid */
#about-text{
    grid-column: 1 / -1;
}

#experiences-text{
    grid-column: 1/-1;
}

/* LETTER POSITIONING */
.main-we,
.main-are,
.main-line,
.main-about,
.main-experiences{
    position: relative;
    display: flex;
    justify-content: space-around;
    margin-left: 4rem;
    margin-right: 4rem;
    padding: 2rem;
    transition: opacity 1s cubic-bezier(0,0,0,1);
}

.main-line{
    display: block;
    height: 3px;
    border: 0;
    border-top: 0.5rem solid black;
    padding-bottom: 0;

    display: flex;
    align-self: center;
    z-index: 2;
}

.main-line__text{
    color: white;
    font-family: "sans-serif";
    font-size: 1.7rem;
    font-weight: 600;
    letter-spacing: .2rem;
    visibility: hidden;
    -webkit-transform: translateY(-6rem);
            transform: translateY(-6rem);
}

/* BOXES COVERING TEXT */
.main-we::after,
.main-are::after,
.main-about::after,
.main-experiences::after{
    content: "";
    position: absolute;
    bottom: 3rem;
    left: 0;
    height: 60%;
    width: 100%;
    background-color: black;
    transition: all 1s cubic-bezier(0,0,0,1);
}

/* CSS to hide the box  */
.hidden::after{
    opacity: 0;
    height: 0;
}

/* Box Animation */
.main-we:hover::after,
.main-are:hover::after,
.main-about:hover::after,
.main-experiences:hover::after{
    height: 0%;
}



/* Text Scale Animation */
.main-we span,
.main-line,
.main-are span,
.main-about span,
.main-experiences span{
    transition: all 1s cubic-bezier(0,0,0,1);
}

/* Hover effect */
.main-we:hover span,
.main-are:hover span,
.main-about:hover span,
.main-experiences:hover span{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

/* Hide text when it becomes too small and starts collapsing */
@media only screen and (max-width: 768px){
    .main-line__text{
        opacity: 0;
    }
}
.sidebar{
    grid-column: sidebar-start / sidebar-end;
    grid-row: 2 / -1;

    /* Flexbox */
    display: flex;
    flex-direction: column;
    justify-content: flex-end; 
}

.sidebar-text{
    color: white;
    transform: rotate(-90deg);
    /* Safari */
    -webkit-transform: rotate(-90deg);
    /* Firefox */
    -moz-transform: rotate(-90deg);
    /* IE */
    -ms-transform: rotate(-90deg);
    
    -webkit-transform-origin: left top 0;
    
            transform-origin: left top 0;
    display: inline-block;

    white-space: pre;
    padding: 1rem;
    margin-bottom: 7rem;

    font-size: 1.5rem;
}
